@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap');

* {
  font-family: 'Popins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: .2s linear;
}

$green: #bac34e;
$black: #222222;
$white: #ffffff;
$light-color: #666666;
$light-bg: #f3f3f3;
$border: .1rem solid rgba(0,0,0,.1);
$box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);

@mixin grid($val) {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax($val, 1fr));
  gap: 1.5rem;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.btn {
  margin-top: 1rem;
  display: inline-block;
  padding: .8rem 3rem;
  background: $green;
  color: $white;
  font-size: 1.7rem;
  cursor: pointer;

  &:hover {
    background: $black;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: $white;
  box-shadow: $box-shadow;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 9%;

  .logo {
    font-size: 2.5rem;
    font-weight: bold;
    color: $black;

    i {
      color: $green;
      padding-right: .5rem;
    }
  }

  .navbar {
    a {
      font-size: 1.7rem;
      color: $light-color;
      margin: 0 1rem;

      &:hover {
        color: $green;
      }
    }
  }

  .icons i {
    font-size: 2.5rem;
    margin-left: 1.7rem;
    cursor: pointer;
    color: $light-color;

    &:hover {
      color: $green;
    }
  }

  .search-form {
    position: absolute;
    top: -115%;
    right: 2rem;
    width: 50rem;
    border-radius: .5rem;
    display: flex;
    align-items: center;
    height: 5.5rem;
    box-shadow: $box-shadow;
    background: $white;

    &.active {
      top: 115%;
    }

    input {
      height: 100%;
      width: 100%;
      padding: 0 1.2rem;
      font-size: 1.6rem;
      color: $black;
      text-transform: none;
    }

    label {
      font-size: 2.5rem;
      margin-right: 1.7rem;
      cursor: pointer;
      color: $light-color;

      &:hover {
        color: $green;
      }
    }
  }

  .shopping-cart {
    position: absolute;
    top: 115%;
    right: 2rem;
    background: $white;
    border-radius: .5rem;
    box-shadow: $box-shadow;
    width: 35rem;
    padding: 2rem;

    .box {
      display: flex;
      align-items: center;
      gap: 1.5rem;
      margin-bottom: 1.5rem;
      position: relative;

      .fa-times {
        position: absolute;
        top: 50%;
        right: 2rem;
        transform: translateY(-50%);
        font-size: 2rem;
        color: $light-color;
        cursor: pointer;

        &:hover {
          color: $green;
        }
      }

      img {
        height: 10rem;
      }

      .content {
        h3 {
          font-size: 1.7rem;
          padding-bottom: .5rem;
          color: $black;
        }

        span {
          font-size: 1.5rem;
          color: $light-color;

          &.multiply {
            margin: 0 .5rem;
          }
        }
      }
    }

    .total {
      color: $light-color;
      text-align: center;
      font-size: 2.2rem;

      span {
        color: $black;
      }
    }

    .btn {
      text-align: center;
      width: 100%;
    }
  }
}

#menu-btn {
  display: none;
}